import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HappyHistoryComponent } from './happy-history.component';

const routes: Routes = [
    {
        path: '',
        component: HappyHistoryComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HappyHistoryRoutingModule {
}
